/*动画*/
@-webkit-keyframes navshow {
from {
    opacity: 0;
}
to {
    opacity: 1;
    visibility: inherit;
}
}
@keyframes navshow {
from {
    opacity: 0;
}
to {
    opacity: 1;
    visibility: inherit;
}
}
@-webkit-keyframes resshow {
from {
    opacity: 1;
}
to {
    opacity: 0;
    visibility: hidden;
}
}
@keyframes resshow {
from {
    opacity: 1;
}
to {
    opacity: 0;
    visibility: hidden;
}
}

